





































import Vue from 'vue';
import { Note } from '@/models/note.interface';
import { Prop, Component, Emit } from 'vue-property-decorator';

@Component
export default class NoteItem extends Vue {
    @Prop() note!: Note;

    editMode = false;

    toggleEditMode(): void {
        this.editMode = !this.editMode;
    }

    @Emit('editNote')
    editNote() {
        this.toggleEditMode();
        return this.note;
    }

    @Emit('removeNote')
    removeNote() {
        return this.note.id;
    }
}
